@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
#nav-bar {
    position: absolute;
    top: 5vh;
    right: 25vw;

    margin: 0;
    padding: 0;
    -webkit-padding-start: 0;
            padding-inline-start: 0;

    width: 25vw;
    height: 10vh;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.link {
    font: normal 1em;
}
:root {
    --title-color: #42495B;
    --purple: #7E76F9;
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    overflow-x: hidden;
}

ul {
    list-style: none;
}

.link {
    text-decoration: none;
    color: #FFFFFF;
}

.page {
    width: 100vw;
    height: 100vh;
}

.main-page-title {
    font-size: 2em;
    color: #42495B;
    color: var(--title-color);
    font-weight: 600;
}

.subtitle {
    color: #7E76F9;
    color: var(--purple);
}

.light-paragraph {
    color: #7A839E;
}

/* Card style */
.cards-conteiner {
    margin: 10vh auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.card {
    position: relative;
    margin: auto 1em;
    margin-bottom: 20vh;
    
    width: 23vw;
    height: 70vh;
    padding: 1em;

    display: flex;
    flex-direction: column;
    align-items: center;

    border-radius: 2em;
    box-shadow: 1px 5px 10px #e9e9e9;
}

.card-title {
    font-size: 1.2em;
    color: #42495B;
    color: var(--title-color);
}

/* End of card style*/
.purple-button {
    margin-top: 1.5em;

    width: 15vw;

    padding: 0.8em;

    background-color: #7E76F9;
    color: #FFFFFF;
    font-weight: 500;

    border: none;
    border-radius: 2em;
}
#home-content {
    padding: 0 5em;

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100vw;
    height: 100vh;
}

.home-social-icons {
    position: absolute;
    bottom: 20vh;

    height: 30vh;
    width: 2vw;
}

#img-content, #description {
    margin-top: 10vh;
}

#description {
    width: 40vw;
}

.word-break {
    margin-top: 2vh;
    width: 30vw;
    display: flex;
    flex-direction: column;
}

#home-img {
    width: 30vw;
    margin-right: 2em;
}

.social-media-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    list-style: none;
}
.card-img, .card-title {
    margin-bottom: 5vh;
}

.service-card-btn {
    position: absolute;
    right: 4vw;
    bottom: 5vh;
    width: 8vw;
}

.service-description {
    padding: 0 1.5em;
    font-size: 0.9em;
    color: #42495B;
}
#services-page {
    margin: 10vh auto;

    width: 90vw;
    height: auto;
}

.services-title {
    margin-left: 3vw;
}
#about-page {
    margin: 0 auto;

    display: flex;
    justify-content: space-evenly;

}

#about-img {
    width: 30vw;
}

#about-content {
    margin-top: 25vh;
    width: 40vw;

    text-align: left;

    
}

#about-content h2 {
    margin: 0.2em 0;
}

#last-paragraph {
    margin-top: 2em;
}

#see-more {
    width: 12vw;
}


#footer-content {
    margin: 0 10vw;

    position: absolute;
    bottom: 0;

    width: 80vw;
    padding: 2em 0 2em 0;

    display: flex;
    justify-content: space-evenly;

    color: #FFFFFF;
    background: transparent linear-gradient(141deg, #867EFF 0%, #6159E5 100%) 0% 0% no-repeat padding-box;
    border-top-left-radius: 10em;
    border-top-right-radius: 10em;    
}

#footer-logo {
    margin: 2.5vh 0;
    font-size: 1.8em;
}

#stay-in-touch-title {
    margin-bottom: 4vh;

    font-weight: 500;
    font-size: 1.5em;
}

.footer-social-icons {
    flex-direction: row;
    justify-content: space-between;

    width: 15vw;
}

.footer-social-icons .instagram {
    padding: 0.2em;
    border: 1px solid #FFFFFF;
    border-radius: 5px;
}

.footer-section-title {
    margin-bottom: 5vh;
    font-weight: 500;
    font-size: 1.2em;
    text-align: left;
}

#footer-content li {
    margin-bottom: 2vh;

    text-align: left;
    font-size: 0.9rem;
}

#hours-info {
    width: 13vw;
    display: flex;
    justify-content: space-between;
}

#contact-info {
    width: 10.5vw;
    display: flex;
    justify-content: space-between;
}

.location-item {
    margin-top: 8vh;
}
#contact-page {
    position: relative;

    padding-top: 20vh;
    text-align: center;
}

.contact-page-title {
    font-weight: normal;
    color: var(--title-color);
}

.special-news {
    margin-left: 0.2em;
    font-size: 1.2em;
}

#email-input {
    margin-right: 2vw;
    
    width: 30vw;
    height: 1vh;
    padding: 1.5em;

    border: none;
    background-color: #F6F7FB;
    border-radius: 5px;
}

#email-input::-webkit-input-placeholder{
    color: #c7c5c5;
}

#email-input:-ms-input-placeholder{
    color: #c7c5c5;
}

#email-input::-ms-input-placeholder{
    color: #c7c5c5;
}

#email-input::placeholder{
    color: #c7c5c5;
} 

.subscribe-btn {
    width: 10vw;
}
#home-page {
    display: flex;
    justify-content: space-between;

    background: linear-gradient(to bottom left, #9992fa 20%, #ffffff 100%);}

#home-logo {
    position: absolute;
    left: 5em;
    top: 2em;

    font-size: 1.3em;
    font-weight: 600;

    color: var(--title-color);
}
.price-card {
    background-color: #FFFFFF;

    display: flex;
    justify-content: space-evenly;
}

.price-card-btn {
    width: 10vw;

    font-weight: normal;
    border: 0.1px solid var(--purple);
    color: #000000;
    background-color: #FFFFFF;
}

.price-card-btn:hover {
    color: #FFFFFF;
    background-color: var(--purple);
}

.card-price-description {
    width: 22vw;
    height: 13vh;
    overflow: hidden;
}

.card-price-description:hover {
    overflow: visible;
    height: 40vh;
}

.amount {
    color: var(--purple) ;
    font-size: 1.8em;
    font-weight: 600;
}
#prices-page {
    padding-top: 5vh;

    height: auto;

    text-align: center;
    
    background-color: #F6F7FB;
}

.cards-conteiner {
    position: relative;
    width: 90vw;
    margin-bottom: 0;
}

#carousel-btns {
    position: absolute;
    bottom: 10vh;
    left: 43vw;
    display: flex;
}

.carousel-btn {
    margin-right: 1em;
    width: 0.8vw;
    height: 2vh;
    border: 0.1px solid var(--purple);
    border-radius: 50%;
    outline: none;
}

.current-carousel-btn {
    background-color: var(--purple);
}
